import { Component  ,ViewChild} from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController,
   NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
import { IonicSelectableComponent } from 'ionic-selectable';


@IonicPage()
@Component({
  selector: 'page-unidadplan4-create',
  templateUrl: 'unidadplan4-create.html',
})
export class unidadplan4CreatePage {
  @ViewChild('portComponent') portComponent: IonicSelectableComponent;
  ports: any[];
  port: any;


  public form                  : FormGroup;
  username: string = "";
  public idtipo: string="";
  public idtipo3: string="";
  public idtipo2: string="";
  public idnombre: string="";
  public idnombre2: string="";
  public idcodigo2: string="";
  public idnombre3: string="";
  public idtipogrupo: string="";
  public iddia               : any;
  public idhora                 : any;

  public ididiomatt: string="";
  public idtipogrupott: string="";
  public idprofesortt: string="";
  idempresa:any;
  public ididioma                 : any;
  public idedificio                 : any;
  public vidioma  :any=[];
  public vidtipogrupo  :any=[];
  public idedificio2		  :any=[];
  public idsalon2		  :any=[];
codigocurso:any;

  public idsalon               : any;

  public ididdia               : any;
  public idprofesor               : any;
  public capacidad                : any;
  public asignados                : any;

  public tcapacidad                : any;
  public vgrupo  :any=[];
  public vgrupo2  :any=[];
  public vgrupo3  :any=[];

  public vidnivel  :any=[];
  public vidjornada  :any=[];
  public vididhorario  :any=[];
  public vididdia  :any=[];
  public vidprofesor  :any=[];



  private _ID                  : String;
  public pageTitle           	: string;

  constructor( public storage: Storage,public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController,
          public events: Events)
  {
     // Define form validation - VERY basic!
     this.form = this._FB.group({
      ididioma     : ['', Validators.required],
      idtipogrupo     : ['', Validators.required],
       idedificio     : ['', Validators.required],
       idsalon     : ['', Validators.required],
       iddia     : ['', Validators.required],
       idhora     : ['', Validators.required],
       idprofesor     : ['', Validators.required],
       codigocurso     : ['', Validators.required],
       capacidad     : [''],
       asignados     : [''],




     });
     this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value

  })



  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }

  ionViewDidLoad() : void
  {
    this.idtipo=this.navParams.data.record2.idtipo;
    this.idtipo2=this.navParams.data.record2.idtipo2;
    this.idtipo3=this.navParams.data.record2.idtipo3;
    this.idnombre=this.navParams.data.record2.idnombre;
    this.idnombre2=this.navParams.data.record2.idnombre2;
    this.idcodigo2=this.navParams.data.record2.idcodigo2;
    this.idnombre3=this.navParams.data.record2.idnombre3;




    if(this.navParams.get("record")==null)
    {

      this.authService.getReg(this.idtipo+'/'+ this.idtipo,'unidadidioma3s').then((result) => {
        this.vidioma=result;
      }, (err) => {
      this.msj.showError3('Error',err.error);
    });

    this.authService.getReg(this.idtipo+'/'+ this.idtipo,'unidadtipogrupo3s').then((result) => {
      this.vidtipogrupo=result;
    }, (err) => {
    this.msj.showError3('Error',err.error);
  });





                          this.authService.getReg(this.idtipo+'/'+ this.idtipo,'unidadedificio3s').then((result) => {
                            this.vgrupo=result;
                          }, (err) => {
                          this.msj.showError3('Error',err.error);
                        });



                        this.authService.getReg( '5d6c9aa1dba66a1c88514638/unidad/'+ this.idempresa+'/' + this.idempresa,'personals').then((result) => {
                          this.vidprofesor=result;

                        }, (err) => {
                        this.msj.showError3('Error',err.error);
                        });


      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
    else {



       this._ID 				=	this.navParams.data.record._id;


    this.authService.getReg(this.idtipo+'/'+ this.idtipo,'unidadtipogrupo3s').then((result) => {
      this.vidtipogrupo=result;
      this.idtipogrupo=	this.navParams.data.record.idtipo._id
      this.idtipogrupott=this.idtipogrupo

    }, (err) => {
    this.msj.showError3('Error',err.error);
  });




       this.authService.getReg(this.idtipo+'/'+ this.idtipo,'unidadidioma3s').then((result) => {
        this.vidioma=result;
        this.ididioma=	this.navParams.data.record.ididioma._id
        this.ididiomatt=this.ididioma
      }, (err) => {
      this.msj.showError3('Error',err.error);
    });



       this.iddia=	this.navParams.data.record.iddia
       this.idhora=	this.navParams.data.record.idhora
       this.codigocurso=	this.navParams.data.record.codigocurso

  this.authService.getReg(this.idtipo+'/'+ this.idtipo,'unidadedificio3s').then((result) => {
    this.vgrupo=result;
    this.idedificio=	this.navParams.data.record.idedificio.id +','+this.navParams.data.record.idedificio.nombre;
    this.idedificio2=this.idedificio.split(',');

  }, (err) => {
  this.msj.showError3('Error',err.error);
});



       this.authService.getReg(this.idtipo+'/'+ this.idtipo+'/'+  this.navParams.data.record.idedificio.id,'unidadedificiosalon3s').then((result) => {
        this.vgrupo2=result;
        this.idsalon   =this.navParams.data.record.idsalon.id +','+this.navParams.data.record.idsalon.nombre;
        this.idsalon2=this.idsalon.split(',');
      }, (err) => {
      this.msj.showError3('Error',err.error);
    });








this.authService.getReg( '5d6c9aa1dba66a1c88514638/unidad/'+ this.idempresa+'/' + this.idempresa,'personals').then((result) => {
  this.vidprofesor=result;
  this.idprofesor=this.navParams.data.record.idprofesor
}, (err) => {
this.msj.showError3('Error',err.error);
});




       this.capacidad=this.navParams.data.record.capacidad
       this.asignados=this.navParams.data.record.asignados

       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
  }



  manageGallery() : void
  {
  let creat='Actualiza';
  let asigna=0;

    if(this.navParams.get("record")==null)
    {  creat='Crea';
    asigna=0;
    }
    else
    {

      asigna=this.form.controls['asignados'].value;
    }

     let   options       : any	     = {
      idtipounidad : {id:this.idtipo,nombre:this.idnombre},

      idperiodo : {id:this.idtipo3,nombre:this.idnombre3},
      idedificio : {id:this.idedificio2[0],nombre:this.idedificio2[1]},
      idsalon : {id:this.idsalon2[0],nombre:this.idsalon2[1]},
      ididioma:this.ididiomatt,
      idtipo:this.idtipogrupott,
      codigocurso:this.codigocurso,
     nombre:this.idedificio2[1] + ' ' +this.idsalon2[1] ,


     iddia : this.form.controls['iddia'].value,
     idhora : this.form.controls['idhora'].value,

     idprofesor:this.idprofesortt,

     capacidad : this.form.controls['capacidad'].value,
     asignados:asigna,


     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' Planificacion '}
    };


     if(this.navParams.get("record")==null)
     {
          if(options){
            this.authService.createReg('',options,'unidadplan4s').then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
          }
     }
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'unidadplan4s').then((result) => {
           if (!this.form.valid) { return; }
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }


  onSelectChange(selectedValue: any) {
      this.idedificio2=selectedValue.split(',');
      this.authService.getReg(this.idtipo+'/'+ this.idtipo+'/'+  this.idedificio2[0],'unidadedificiosalon3s').then((result) => {
        this.vgrupo2=result;
      }, (err) => {
      this.msj.showError3('Error',err.error);
    });
        }


  onSelectChange2(selectedValue: any) {



    // this.iddepartamento=aa[0]
      this.idsalon2=selectedValue.split(',');
      for (var i = 0; i < this.vgrupo2.length; i++) {
        if(this.vgrupo2[i]._id==this.idsalon2[0])
        {
          this.tcapacidad=this.vgrupo2[i].capacidad
          this.form.controls['capacidad'].setValue(this.tcapacidad);




        }
      }


        }


        onSelectChangeB(selectedValue: any) {   this.ididiomatt=selectedValue; }
        onSelectChangeC(selectedValue: any) {   this.idtipogrupott=selectedValue; }
  portChange(event: {
    component: IonicSelectableComponent,
    value: any
  }) {

    this.idprofesortt=event.value._id;


  }

}



